	module.exports = {"data":{"markdownRemark":{"html":"<h1>Complementary Angles</h1>\n<h2>Definition</h2>\n<p>Two angles which (when added together) have a sum of 90°, are called complementary angles.</p>\n<p><img src=\"https://raw.githubusercontent.com/pranabendra/articles/master/Complementary-angles/comp1.png\"></p>\n<p>Complementary angles don’t have to be next to each other, just so long as the total is 90°.</p>\n<h3>For Example :</h3>\n<p>In the above figures, diagrams <code>a.</code> and <code>b.</code> show that the complementary angles are <i>adjacent</i>, whereas in <code>c.</code> and <code>d.</code>, the complementary angles are <i>not adjacent</i>.</p>\n<h2>More Information:</h2>\n<p><a href = \"https://www.mathsisfun.com/geometry/complementary-angles.html\">Complementary Angles</a></p>","fields":{"slug":"/mathematics/complementary-angles/"},"frontmatter":{"title":"Complementary Angles"}}},"pathContext":{"slug":"/mathematics/complementary-angles/"}}

